.dashboard {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
}
.panel {
  background-color: #1e1e1e;
  color: white;
  padding: 20px;
  border-radius: 12px;
  flex: 1 1 30%;
  min-width: 280px;
}
input {
  width: 100%;
  padding: 8px;
  margin-top: 10px;
  margin-bottom: 10px;
  background: #333;
  color: white;
  border: 1px solid #444;
  border-radius: 4px;
}
button {
  width: 100%;
  padding: 10px;
  background: #444;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
button:hover {
  background: #555;
}